html {
  height: 100%;
}
html body {
  padding: 0px;
  margin: 0px;
  font-size: 16px;
  background-color: #ccc;
  height: 100%;
}
html body .modal-wrap {
  display: table;
  width: 100%;
  height: 100%;
}
html body .modal-wrap .modal-content-wrap {
  background-color: #ccc;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
html body .modal-wrap .modal-content-wrap .modal-content {
  display: inline-block;
  background-color: orange;
}
